import React, { useContext, useEffect, useState, useCallback, useRef } from 'react';
import { ScrollBar } from '../Scrollbar/ScrollBar';
import { Grid } from '../Grid/Grid';
import { NavDays } from '../NavDays/NavDays';
import { Context } from '../../context/DatesContext';
import { Context as FormContext } from '../../context/FormContext';
import axios from 'axios';
import TextField from '@mui/material/TextField';
import { Context as ThumbContext } from '../../context/ThumbContext';
import { FormContainer } from '../Form/FormContainer';
import { Gallery } from '../pagina/Gallery';

import _ from 'lodash';
import zIndex from '@mui/material/styles/zIndex';

export const Container = () => {
	const { containerWidthMsg } = useContext(ThumbContext);
	const containerRef = useRef();

	const { state, loadIntervals_db_msg, addIntervalToArrayRentedMsg, removeByDragMsg } = useContext(Context);
	const { calendar } = state;
	const { intervalRented } = calendar;

	// const { intervalData } = state;
	const { showForm, socketData } = useContext(FormContext).state;
	console.log(socketData);
	const { socketDataMsg } = useContext(FormContext);
	console.log('CONTAINER REPORTING RELOAD');

	// const getRentedArray = async () => {
	// 	const res = await axios.get(`http://localhost:8000/all`);
	// 	// const res = await axios.get(`/all`);

	// 	loadIntervals_db_msg(res.data);
	// };
	const getRentedArray = async () => {
		const res = await axios.get(`/all`);
		// const res = await axios.get(`/all`);

		loadIntervals_db_msg(res.data);
	};
	// // const ws = new WebSocket(`ws://localhost:8000/ws`);
	const [ data, setData ] = useState({
		email: '',
		end: '2023/01/06',
		id: '63b58f79e18806c7473f348f',
		name: '',
		nights: '1',
		phone: '',
		price: '2000',
		room: '1',
		start: '2023/01/05'
	});
	useEffect(() => {
		var HOST = window.location.origin.replace(/^http/, 'ws');
		const socketUrlwss = `${HOST}/wss`;
		const wsNew = new WebSocket(socketUrlwss);
		wsNew.onopen = () => {
			wsNew.send(JSON.stringify(data));
		};

		wsNew.onmessage = (msg) => {
			console.log(JSON.parse(msg.data));
			const { data } = JSON.parse(msg.data);

			const superData = JSON.parse(data);
			console.log(superData);
			addIntervalToArrayRentedMsg({
				...superData,
				start: new Date(superData.start),
				end: new Date(superData.end)
			});
			socketDataMsg({
				...superData,
				start: new Date(superData.start),
				end: new Date(superData.end)
			});
		};
		getRentedArray();
	}, []);
	console.log(socketData);
	useEffect(
		() => {
			if (socketData) {
				console.log(socketData);
				var HOST = window.location.origin.replace(/^http/, 'ws');
				const socketUrlws = `${HOST}/ws`;
				const ws = new WebSocket(socketUrlws);
				ws.onopen = () => {
					ws.send(JSON.stringify({ ...socketData, room: socketData.room + 1 }));
				};

				ws.onmessage = (msg) => {
					const { data } = JSON.parse(msg.data);

					const superData = JSON.parse(data);
					console.log(superData);

					// removeByDragMsg({
					// 	...superData,
					// 	start: superData.start,
					// 	end: superData.end
					// });
				};
			}
		},
		[ socketData ]
	);

	return (
		<div
			style={{
				display: 'flex',
				justifyContent: 'center',
				alignItems: 'center'
			}}
		>
			<div
				ref={containerRef}
				style={{
					display: 'flex',
					flexDirection: 'column',
					width: '80%'
				}}
			>
				<div style={{ display: 'flex', flexDirection: 'column' }}>
					{/* <Gallery /> */}
					<Grid />
				</div>

				{showForm ? (
					<div
						style={{
							width: '60%',
							position: 'absolute',
							left: '10%',
							top: '7%',

							backgroundColor: 'blue'
						}}
					>
						<FormContainer />
					</div>
				) : null}

				<div style={{}}>
					<ScrollBar />
				</div>
				<NavDays />
				<button />
			</div>
		</div>
	);
};
